import { Component, OnInit } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { ServiceService } from '../../services/service.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.css']
})
export class HomeComponent implements OnInit {

  constructor(public api:ServiceService, public nav:Router) { }
  username: string="";
  password: string="" ;
    manager :string[]=[];
  ngOnInit() {
     this.getManager()
  }
    async getManager(){
    let manager :any = await this.api.getManager();
    localStorage.manager = JSON.stringify(manager['data']);

  }
  async submit(){
    if(!this.username || !this.password){
      alert('אחד השדות ריקים')
      return;
          }else{
            let manager:any = await this.api.findManager(this.username ,this.password);
            console.log(manager)
            this.manager = manager['data'];
            if(this.manager.length >0){
              // alert('נמצא משתמש')
              this.nav.navigate([ 'stock' ]);
              // this.nav.navigate([ 'Managment' ]);

            }else{
              alert('ססמא או שם משתמש לא נכון')
              window.location.reload();
            }
          }






    
  }

}
