import { Component, OnInit } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { ServiceService } from '../../services/service.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-log-in',
  templateUrl: './log-in.component.html',
  styleUrls: ['./log-in.component.css']
})
export class LogInComponent implements OnInit {
  username: string="";
  password: string="" ;
    mainmanager :string[]=[];
  constructor(public api:ServiceService, public nav:Router) {
    this.getMainManager()
   }
 async getMainManager(){
    let mainmanager :any = await this.api.getMainManager();
    localStorage.mainmanager = JSON.stringify(mainmanager['data']);

  }
  ngOnInit(): void {
  }
  async submit(){
    if(!this.username || !this.password){
      alert('אחד השדות ריקים')
      return;
          }else{
            let mainmanager:any = await this.api.findMainManager(this.username ,this.password);
            // console.log(manager)
            this.mainmanager = mainmanager['data'];
            if(this.mainmanager.length >0){
              // alert('נמצא משתמש')
              this.nav.navigate([ 'mainmanu' ]);
              // this.nav.navigate([ 'Managment' ]);

            }else{
              alert('ססמא או שם משתמש לא נכון')
              window.location.reload();
            }
          }
  }
}
