import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import {HomeComponent} from './components/home/home.component'
import {StockComponent} from './components/stock/stock.component'
import {NewitemComponent} from './components/newitem/newitem.component'
import {LogInComponent} from './admin/log-in/log-in.component'
import {InsertToStockComponent} from './admin/insert-to-stock/insert-to-stock.component'
import {InsertManagerComponent} from './admin/insert-manager/insert-manager.component'
import {MainmanuComponent} from './admin/mainmanu/mainmanu.component'
const routes: Routes = [
  {
  path:'',
  component: HomeComponent,
},
  {
  path:'stock',
  component: StockComponent,
},
  {
  path:'addtostock',
  component: NewitemComponent,
},
  {
  path:'admin',
  component: LogInComponent,
},
  {
  path:'inserttostock',
  component: InsertToStockComponent,
},
  {
  path:'insertmanager',
  component: InsertManagerComponent,
},
  {
  path:'mainmanu',
  component: MainmanuComponent,
},

];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
