constructor(private http: HttpClient) { }
//________________________________client function________

getStock() {
  return new Promise(async (resolve, reject) => {
    try {
      await this.http.get(this.baseURL + 'getStock?').subscribe((data) => {
        resolve(data);
      });
    } catch (err) {
      console.log(err);
    }
  });
}
insertStock(stock:any) {
  return new Promise(async (resolve, reject) => {
    try {
      await this.http.post(this.baseURL + 'insertStock', stock).subscribe((data) => {
        resolve(data);
      });
    } catch (err) {
      console.log(err);
    }
  });
}
deleteStock(id:any) {
  return new Promise(async (resolve, reject) => {
    try {
      await this.http.get(this.baseURL + 'deleteStock?' + 'id=' + id).subscribe((data) => {
        resolve(data);
          });
    } catch (err) {
      console.log(err);
    }
  });
}
updateStock(stock:any) {
		return new Promise(async (resolve, reject) => {
			try {
				await this.http.post(this.baseURL + 'updateStock', stock).subscribe((data) => {
					resolve(data);
				});
			} catch (err) {
				console.log(err);
			}
		});
}
