//______________________________manager function________________
findManager(username: string , password: string) {
  return new Promise(async (resolve, reject) => {
    try {
      await this.http
        .get(this.baseURL + 'findManager?' + 'username=' + username + '&password=' + password)
        .subscribe((data) => {
          resolve(data);
        });
    } catch (err) {
      console.log(err);
    }
  });
}
findMainManager(username: string , password: string) {
  return new Promise(async (resolve, reject) => {
    try {
      await this.http
        .get(this.baseURL + 'findMainManager?' + 'username=' + username + '&password=' + password)
        .subscribe((data) => {
          resolve(data);
        });
    } catch (err) {
      console.log(err);
    }
  });
}
